.bg{
    width: 100vw;
    height: 100vh;
    opacity: 0.1;
    position: relative;
}

.bg-container{
    position: absolute;
    left: 0;
    top: 0;
    background: #000;
    z-index: -1;
}

.name{
    color: #fff;
    font-size: 4rem;
    vertical-align: middle;
    font-weight: bolder;
}


.vertical-middle{
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    padding: 135px;
}

.profile-pic-container{
    width: 74%;
    margin-left: 13%;
    box-shadow: rgba(0, 255, 255, 0.49) 0 2px 7px 3px;
}

.summary{
    color: rgba(255, 255, 255, 0.84);
    font-size: 1.8rem;
    margin-bottom: 40px;
    white-space: pre-line;
}

.social-link-icon{
    color: rgba(255, 255, 255, 0.56);
    width: 3rem;
}

.scroll-down {
    transform: translateX(-50%);
    position: absolute;
    left: 50%;
    bottom: -40px;
    z-index: 10;
    cursor: pointer;
}

.scroll-down a {
    padding-top: 80px;
}
.scroll-down a span {
    position: absolute;
    top: 0;
    left: 50%;
    width: 24px;
    height: 24px;
    margin-left: -12px;
    border-left: 1px solid #fff;
    border-bottom: 1px solid #fff;
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
    -webkit-animation: sdb07 2s infinite;
    animation: sdb07 2s infinite;
    opacity: 0;
    box-sizing: border-box;
}
.scroll-down a span:nth-of-type(1) {
    -webkit-animation-delay: 0s;
    animation-delay: 0s;
}
.scroll-down a span:nth-of-type(2) {
    top: 16px;
    -webkit-animation-delay: .15s;
    animation-delay: .15s;
}
.scroll-down a span:nth-of-type(3) {
    top: 32px;
    -webkit-animation-delay: .3s;
    animation-delay: .3s;
}
@-webkit-keyframes sdb07 {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
@keyframes sdb07 {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

.scroll-down a {
    position: absolute;
    bottom: 20px;
    left: 50%;
    z-index: 2;
    display: inline-block;
    -webkit-transform: translate(0, -50%);
    transform: translate(0, -50%);
    color: #fff;
    font : normal 400 20px/1 'Josefin Sans', sans-serif;
    letter-spacing: .1em;
    text-decoration: none;
    transition: opacity .3s;
}

.scroll-down a:hover {
    text-decoration: none;
    opacity: .5;
}
